html {  
height: 100%;
width: 100%;
background-image:
  linear-gradient(
      to bottom right, 
      #1ED793, 
      #1ED760
  );}

.App {
  padding-top:28vh;
  font-size: 18pt;
  width:100vw;}

.logo{
  margin-bottom: 35px;
  text-align: center;
  color: white;
  font-size: 14px;
  font-weight: 800;
}

.App-logo {
  height: 13vmin;
  pointer-events: none;
}

.auth-Text{
  margin-left:2vw;
  align-self: center;
}

.code-container {
  text-align: center;
}

.code-outer-div {
  display: flex;
  width:100vw;
}

.App-link {
  color: #61dafb;
}

.code{
  color: white;
  word-wrap: break-word;
  width:70vw;
  padding:10px;
  margin:30px auto;
}

.btn{
  text-align: center;
  margin:40px auto;
  display: inline-block;
  outline:0;
  color: #1ED793;
  background-color: white;
  font-size: 14px;
  font-weight: 800;
  border-radius: 500px;
  padding: 16px 48px 18px;
  transition-property: background-color,border-color,color,box-shadow,filter;
  transition-duration: .3s;
  border-width: 0;
/*  letter-spacing: 2px;*/
  text-decoration: none;
}

.creds{
  display:flex;
  flex-direction: column;
  justify-content: center;
}

.client-input{
  display: inline-block;
  min-width:100px;
  max-width:500px;
  background-color: white;
  color: black;
  opacity: 0.6;
  border:none;
  margin-left: 20px;
  padding: 5px 5px 5px;
}

::placeholder {
  color: grey;
}